#header {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    color: var(--white);
    border-bottom: 1px solid var(--dark_bd);
    background-color: var(--dark_bg);
    display: flex;
    align-items: center;
    font-family: var(--sub_font);
    z-index: 1000;
}
.header__port {
    flex: 1 1 40%;
    a {
        display: inline-block;
        padding: 1rem;
        text-transform: capitalize;
        position: relative;
        &::before {
            content: '';
            position: absolute;
            left: 1rem;
            bottom: 1rem;
            width: calc(100% - 2rem) ;
            height: 1px;
            background: var(--white);
            transform: scaleX(0);
            transition: all 0.3s ease;
        }
        &:hover::before {
            transform: scaleX(1);
        }
    }
}
.header__logo {
    flex: 1 1 20%;
    text-align: center;
    a {
        font-size: 1.6rem;
        font-family: var(--main_font);
        display: inline-block;
        padding: 1rem;
    }
}
.header__menu {
    flex: 1 1 40%;
    text-align: right;
    li {
        display: inline-block;
        a {
            display: inline-block;
            padding: 1rem;
            position: relative;
            &::before {
                content: '';
                position: absolute;
                left: 1rem;
                bottom: 1rem;
                width: calc(100% - 2rem) ;
                height: 1px;
                background: var(--white);
                transform: scaleX(0);
                transition: all 0.3s ease;
            }
            &:hover::before {
                transform: scaleX(1);
            }
        }
    }
}

// 라이트 버전
.light#header {
    background-color: var(--light_bg);
    border-color: var(--light_bd);
    color: var(--black);
    a {
        &::before {
            background: var(--black);
        }
    }
}

// 미디어 커리
@media (max-width: 1200px) {
    .header__menu {display: none;}
    .header__port {display: none;}
}
@media (max-width: 600px){
    #header {
        background: rgba(0,0,0,0.4);
        backdrop-filter: blur(40px);
    }
}

// header Animation
#header {
    top: -100%;
}